.Order__Order__W-Npf {
  width: 80%;
  border: 1px solid #ccc;
  box-shadow: 0 2px 3px #eee;
  padding: 10px;
  margin: 10px auto;
  box-sizing: border-box;
}

.Modal__Modal__cd320 {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 70%;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 15%;
  top: 30%;
  box-sizing: border-box;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
@media (min-width: 600px) {
  .Modal__Modal__cd320 {
    width: 500px;
    left: calc(50% - 250px);
  }
}

.Spinner__loader__3qv_p,
.Spinner__loader__3qv_p:before,
.Spinner__loader__3qv_p:after {
  border-radius: 50%;
}
.Spinner__loader__3qv_p {
  color: #000000;
  font-size: 11px;
  text-indent: -99999em;
  margin: 55px auto;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.Spinner__loader__3qv_p:before,
.Spinner__loader__3qv_p:after {
  position: absolute;
  content: '';
}
.Spinner__loader__3qv_p:before {
  width: 5.2em;
  height: 10.2em;
  background: #000000;
  border-radius: 10.2em 0 0 10.2em;
  top: -0.1em;
  left: -0.1em;
  -webkit-transform-origin: 5.2em 5.1em;
  transform-origin: 5.2em 5.1em;
  -webkit-animation: Spinner__load2__1sg5x 2s infinite ease 1.5s;
  animation: Spinner__load2__1sg5x 2s infinite ease 1.5s;
}
.Spinner__loader__3qv_p:after {
  width: 5.2em;
  height: 10.2em;
  background: #000000;
  border-radius: 0 10.2em 10.2em 0;
  top: -0.1em;
  left: 5.1em;
  -webkit-transform-origin: 0px 5.1em;
  transform-origin: 0px 5.1em;
  -webkit-animation: Spinner__load2__1sg5x 2s infinite ease;
  animation: Spinner__load2__1sg5x 2s infinite ease;
}
@-webkit-keyframes Spinner__load2__1sg5x {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes Spinner__load2__1sg5x {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

